	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Sweet Acidic Smell of Success","date":"27 Mar 2002","slug":"/2002-03-27-sweet-acidic-smell-of-success/","tags":"Y2002"},"html":"<p>It's 2 am, the 3rd set of bedding is in the wash, and it looks like the parent with the more flexible but less successful career (me) will be staying home with Christopher today.</p>\n<p>7 am: maybe it was just a slight food poisoning after all. Christopher woke up active and happy. We'll take him to day care after all. Meanwhile, Papa never did get back to sleep.</p>"},"next":{"fields":{"title":"GI Christopher","date":"26 Mar 2002","slug":"/2002-03-26-gi-christopher/"}},"previous":{"fields":{"title":"Quarter To Six","date":"30 Mar 2002","slug":"/2002-03-30-quarter-to-six/"}}},{"node":{"fields":{"title":"GI Christopher","date":"26 Mar 2002","slug":"/2002-03-26-gi-christopher/","tags":"Y2002"},"html":"<p>My evening was crowned by Mama coming home an hour later than I expected, and by Christopher puking his guts out on the couch about the time I was expecting her. Hopefully it was a mild food poisoning (the yoghurt is the main subject), and not the start of a GI infection.</p>"},"next":{"fields":{"title":"If you can't beat 'em","date":"25 Mar 2002","slug":"/2002-03-25-if-you-cant-beat-em/"}},"previous":{"fields":{"title":"Sweet Acidic Smell of Success","date":"27 Mar 2002","slug":"/2002-03-27-sweet-acidic-smell-of-success/"}}},{"node":{"fields":{"title":"If you can't beat 'em","date":"25 Mar 2002","slug":"/2002-03-25-if-you-cant-beat-em/","tags":"Y2002"},"html":"<p>In my tries to produce XML files of this website, the obvious answer finally hit me this evening. If I'm trying to get a tool for Radio 7 to work, the thing to do is to download and install Radio 7. I've now got a folder of all the files I need. ;-)</p>\n<p><a href=\"http://www.vfth.com/2002/03/25#MTowODowNyBQTQdbdb\">Cookies for breakfast?</a> Christopher's currently on a pickle kick. I guess that's not quite as bad.</p>\n<p>Doc calls America West <a href=\"http://doc.weblogs.com/2002/03/23#americaWaste\">\"America Waste\"</a> after an ill-fated check-in from LAX to Phoenix. I wanted us to take America West LAX-LAS-PHX-LAX next month. After flying 12 hours from Frankfurt with a 2-year-old, do we really want to endure a check-in from hell? But AmWest costs half as much as the competition, even cheaper than Southwest. Decisions, decisions.</p>"},"next":{"fields":{"title":"Finding The Door","date":"24 Mar 2002","slug":"/2002-03-24-finding-the-door/"}},"previous":{"fields":{"title":"GI Christopher","date":"26 Mar 2002","slug":"/2002-03-26-gi-christopher/"}}}],"pathPrefix":"","first":false,"last":false,"index":913,"pageCount":1101}}

